@import url(https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Frank+Ruhl+Libre&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    text-align: center;
}
.App-header {
    font-family: 'Bodoni Moda', serif;
    background-color: #000000;
    height: 12vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.app-link {
    color: #61dafb;
    text-decoration: none;
    font-size: 24px;
    font-weight: 300px;
    margin-right: 30px;
    margin-left: 30px;
}
.app-link :hover {
    cursor: pointer;
}
.active-link {
    color: rgb(189, 189, 75);
    font-weight: 400;
}

.main-page {
    display: flex;
    justify-content: space-between;
    background-color: #000000;
    width: 100%;
}
img {
    height: 670px;
}

.posts {
    color: white;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-image: url('https://www.osae.info/documentos_osae/2012/2012_03_18_m51.jpg');
    background-size: cover;
    background-repeat: no-repeat;
}
.post {
    background-color: #333376;
    border: thin #808080bb solid;
    width: 700px;
    height: 430px;
    padding: 10px;
    font-size: 140%;
    padding-bottom: 40px;
    margin-top: 10px;
    border-radius: 20px;
}
.author {
    display: flex;
    height: 15%;
    align-items: flex-start;
}
.author img {
    border-radius: 100px;
    max-height: 100px;
    max-width: 100px;
    margin-left: 10px;
    margin-right: 20px;
}
.author p {
    margin: 10px 0 0 20px;
}
.name {
    font-weight: bold;
}
.autor-information {
    color: rgba(255, 255, 255, 0.631);
}
.content {
    display: flex;
    flex-direction: column;
    margin-top: 0px;
    margin-left: 100px;
    text-align: left;
}
.content-text {
    margin-top: -10px;
    margin-bottom: 20px;
    margin-left: 20px;
}
.content img {
    width: 600px;
    height: 350px;
    margin-left: -30px;
    margin-right: 20px;
    border-radius: 30px;
}
.badges i {
    padding: 3px;
    font-size: 2rem;
    transition: 0.4s;
}
.badges i:hover {
    color: red;
}
i.fas {
    color: red;
}

.notFound {
    font-family: 'Bodoni Moda', serif;
    background-color: black;
}
h1 {
    margin: 0;
    color: white;
}
.notFound img {
    height: 575px;
    width: auto;
}

.app {
    font-family: 'Frank Ruhl Libre', serif;
    height: 800px;
    color: white;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: auto;
    background-image: url('https://www.osae.info/documentos_osae/2012/2012_03_18_m51.jpg');
    background-size: cover;
    background-repeat: no-repeat;
}
.search {
    background-color: #3c3ccf;
    width: 350px;
    height: 70px;
    margin: 10px 0px 0px 0px;
    text-align: center;
    border-radius: 10px;
}
.search input {
    margin-top: 15px;
    font-size: 170%;
    cursor: pointer;
}
.contacts {
    width: 350px;
}

.contact {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    background-color: #3c3ccf;
    text-align: center;
    font-size: 120%;
    padding: 10px;
    border-radius: 20px;
}
.contact p {
    margin: 0px;
}
.gender {
    width: 70px;
    height: 70px;
}
.name {
    font-weight: bold;
    font-size: 130%;
}
.genderPanel {
    width: 350px;
    height: 60px;
    display: flex;
    justify-content: space-between;
    background-color: #3c3ccf;
    margin: 5px 10px 10px 10px;
    border-radius: 10px;
    padding: 2px;
}
.genderPanel button {
    font-size: 170%;
    width: 120px;
    height: 50px;
    border: none;
    cursor: pointer;
    margin: 5px;
    border-radius: 10px;
}
.genderPanel button:hover {
    color: black;
    transition: all 300ms ease;
    background-color: white;
    font-size: 180%;
    height: 50px;
    border: none;
    cursor: pointer;
    margin: 5px;
    border-radius: 10px;
}

.genderChecked {
    background-color: #000000;
    color: white;
}
.genderUnchecked {
    background-color: white;
    border: none;
}
.contact-link {
    text-decoration: none;
    color: black;
}

.contactDescription {
    background-image: url('https://www.osae.info/documentos_osae/2012/2012_03_18_m51.jpg');
    background-size: cover;
    font-family: 'Bodoni Moda', serif;
    color: black;
    height: 664px;
    width: auto;
    display: flex;
    justify-content: center;
}
p {
    margin: 0;
    color: white;
}
.card {
    background-color: #3c3ccf;
    margin-top: 30px;
    height: 300px;
    width: 400px;
    font-size: 120%;
    padding: 10px;
    border-radius: 20px;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}
.contactDescription img {
    border-radius: 100px;
    width: 100px;
    height: 100px;
    margin-left: 10px;
    margin-right: 20px;
}

